.calc-app {
  margin: 0 auto;
  margin-top: 40px;
  width: 258px;
  text-align: center;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}
input {
  height: 60px;
  width: 250px;
  border-top: 1px solid;
  outline: none;
  font-size: 20px;
  word-wrap: break-word;
}
#clear {
  grid-column: 1/3;
  grid-row: 1;
}
#result {
  grid-column: 3/5;
  grid-row: 5;
}
p {
  margin-bottom: 0px;
}
